<template>
  <div>
    <div
      v-for="item in nav"
      :key="item.index"
      @contextmenu.prevent="contain.handleContextMenu($event,item)"
    >
      <avue-draggable
        v-if="!item.children"
        v-bind="renameTitle(item)"
        :scale="container.stepScale"
        :disabled="!contain.menuFlag"
        :step="container.stepScale"
        :width="item.component.width"
        :height="item.component.height"
        :ref="common.DEAFNAME+item.index"
        :id="common.DEAFNAME+item.index"
        v-show="!item.display"
        @focus="handleFocus"
        @blur="handleBlur"
        @mouseover.native="handleOver(item.index)"
        @mouseout.native="handleOut(item.index)"
      >
        <component
          :ref="common.NAME+item.index"
          :id="common.NAME+item.index"
          :is="common.COMPNAME+item.component.name"
          v-bind="renameTitle(item)"
          :data-formatter="getFunction(item.dataFormatter)"
          :label-formatter="getFunction(item.labelFormatter)"
          :formatter="getFunction(item.formatter)"
          :width="item.component.width"
          :data-query="getJson(item.dataQuery)"
          :height="item.component.height"
          :animation="!contain.menuFlag"
          :disabled="!contain.menuFlag"
          :scale="container.stepScale"
          :option="item.option"
          :home-url="contain.config.url"
          :click="handleClick"
        />
        <el-button type="text" @click="changeVisState(item.index)" v-show="scaleFlag[item.index] && !contain.menuFlag" icon="el-icon-zoom-in" circle></el-button>
      </avue-draggable>
      <el-dialog
          :modal-append-to-body='false'
          :visible.sync="centerDialogVisible[item.index]"        
          :width="diaWidth(item.component.width * 2+50)"
          :title="item.title"
          center
          >
          <component
            :ref="common.NAME+item.index"
            :id="common.NAME+item.index"
            :is="common.COMPNAME+item.component.name"
            v-bind="renameTitle(item)"
            :width="item.component.width * 2"           
            :height="item.component.height * 2"
            :option="item.option"
        />
        </el-dialog>
      <subgroup :nav="item.children"></subgroup>
    </div>
  </div>
</template>

<script>
import { addUrlParam } from '@/utils/utils'
import common from '@/config'
export default {
  name: 'subgroup',
  inject: ["contain", 'container'],
  provide() {
    return {
      contain: this.contain,
      container: this.container
    };
  },
  props: {
    nav: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      common: common,
      centerDialogVisible:{},
      scaleFlag:{}
    }
  },
  created() {
    sessionStorage.removeItem("param")
    sessionStorage.removeItem("active")
  },
  mounted() {
    window.addEventListener("message", event => {
      if (!!event.data.data) {
        sessionStorage.setItem("param", event.data.data)
      }
      if (!!event.data.active) {
        sessionStorage.setItem("active", event.data.active)
      }
    });
    var username = this.container.getUrlData();
    if (!!username.dpparam) {
      sessionStorage.setItem("param", username.dpparam)
    }
    if (!!username.active) {
      sessionStorage.setItem("active", username.active)
    }
  },
  computed:{
    diaWidth(){
      return width=>this.setPx(width)
    }

  },
  methods: {
    getFunction(fun) {
      if (!this.validatenull(fun)) return eval(fun);
    },
    getJson(str) {
      if (!this.validatenull(str)) return JSON.parse(str);
    },
    //点击事件交互
    handleClick({ type, child, value }) {
      if (type === "tabs") {
        const indexList = child.index;
        indexList.forEach(index => {
          const paramName = child.paramName;
          const item = this.contain.findlist(index);
          if (item.url) {
            item.url = addUrlParam(item.url, paramName, value);
            this.$refs[this.common.NAME + index].forEach(ele => {
              ele.updateData();
            });
          }
        });
      }
    },
    //刷新数据
    handleRefresh(tip = true) {
      this.$refs[this.common.NAME + this.contain.activeObj.index][0].updateData();
      if (tip) {
        this.$message.success('刷新成功')
      }
    },
    //获取对象
    handleGetObj(val) {
      return this.$refs[`${this.common.DEAFNAME}${val}`];
    },
    handleFocus({ index }) {
      this.container.gradeFlag = true;
      if (this.contain.keys.ctrl) {
        if (!Array.isArray(this.contain.active)) {
          this.contain.handleInitActive();
        }
        this.contain.active.push(index);
      } else {
        if (!this.contain.active.includes(index)) {
          this.contain.active = [index];
        }
      }
    },
    handleBlur({ left, top, width, height }) {
      this.container.gradeFlag = false;
      this.$set(this.contain.activeObj.component, 'width', width)
      this.$set(this.contain.activeObj.component, 'height', height)
      this.$set(this.contain.activeObj, 'left', left)
      this.$set(this.contain.activeObj, 'top', top)
    },
    renameTitle(item){
      let {title,...params} = item;
      return {
        componentTitle: title,
        ...params
      }
    },
    changeVisState(index){
        this.$forceUpdate();
        this.$set(this.centerDialogVisible,index,true);
    },
    handleOver(index){
      this.$set(this.scaleFlag,index,true);
    },
    handleOut(index){
      this.$set(this.scaleFlag,index,false);
    }
  }
}
</script>

<style lang="scss">
@import "../../styles/font.css";
</style>