      <codeedit ref="codeedit" v-model="code.obj"></codeedit>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="code.box=false">取 消</el-button>
        <el-button type="primary" @click="codeClose" size="small">确 定</el-button>
