<template>
  <el-container class="list">
    <el-aside width="230px">
      <h2 class="title">数据大屏</h2>
      <el-menu
        :default-active="activeName"
        background-color="#171b22"
        text-color="#fff"
        @select="handleSelect"
        active-text-color="#00baff"
      >
        <el-menu-item index="1">
          <i class="el-icon-document"></i>
          大屏管理
        </el-menu-item>
        <el-menu-item index="2">
          <i class="el-icon-document"></i>
          地图管理
        </el-menu-item>
        <el-menu-item index="3">
          <i class="el-icon-document"></i>
          分类管理
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-dialog title="登录" :visible.sync="centerDialogVisible" width="30%" center>
      <el-input placeholder="请输入账号" v-model="userName" clearable>
        <template slot="prepend">账号</template>
      </el-input>
      <div style="margin: 20px 0;"></div>
      <el-input placeholder="请输入密码" v-model="password" show-password>
        <template slot="prepend">密码</template>
      </el-input>
      <div style="margin: 40px 0;"></div>
      <el-form>
        <el-form-item>
          <el-button type="primary" @click="checkLoginMes" style="width:100%;">登录</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-scrollbar v-if="!centerDialogVisible" class="list" style="width:100%;height:800px;">
      <list v-if="activeName==1"></list>
      <maps v-if="activeName==2"></maps>
      <category v-if="activeName==3"></category>
    </el-scrollbar>
  </el-container>
</template>
<script>
import list from './list/index'
import maps from './list/map'
import category from './list/category'
import { login } from '@/api/visual';
export default {
  name: "index",
  components: {
    list,
    maps,
    category
  },
  data() {
    return {
      activeName: '1',
      centerDialogVisible: true,
      userName: "",
      password: ""
    }
  },
  created() {
    if (!!sessionStorage.getItem("userId")) {
      this.centerDialogVisible = false;
    }
  },
  methods: {
    checkLoginMes() {
      if (!this.userName) {
        this.$message({
          message: '账号不能为空',
          type: 'error',
          center: true
        });
        return;
      }
      if (!this.password) {
        this.$message({
          message: '密码不能为空',
          type: 'error',
          center: true
        });
        return;
      }
      const loading = this.$loading({
        lock: true,
        text: '正在加载中，请稍后',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      // setTimeout(() => {
      //   loading.close();
      //   
      // }, 3000)
      login({
        username: this.userName,
        password: this.password
      }).then(res => {
        loading.close();
        if (res.data.code == 200 || res.data.code == '200') {

          if (res.data.data.status == 1) {
            sessionStorage.setItem("userId", res.data.data.userId);
            this.centerDialogVisible = false;
          } else {
            this.$message({
              message: res.data.data.msg,
              type: 'error',
              center: true
            });
          }
        } else {
          this.$message({
            message: res.data.msg,
            type: 'error',
            center: true
          });
        }

      })

    },
    handleSelect(key) {
      this.activeName = key;
    },
  }
}
</script>
<style lang="scss" scoped>
.title {
  font-size: 20px;
  color: #fff;
  text-align: center;
  line-height: 60px;
  font-weight: 500;
}
</style>